var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_c('b-img',{staticClass:"mt-4",attrs:{"src":require("@/assets/img/escritoras/carmeriera2.jpg"),"fluid":"","center":""}}),_c('h1',[_vm._v("Carme Riera Guilera")]),_c('p',[_vm._v("(Palma de Mallorca, 12 de enero de 1948) es una escritora española que escribe en catalán y castellano, guionista, ensayista, profesora y ocupa la silla \"n\" en la Real Academia Española. Ha presidido CEDRO desde junio de 2015 a junio de 2019.")]),_c('p',[_vm._v("Carme Riera pasó su infancia y adolescencia en Palma de Mallorca, donde tiene vínculos familiares con el ingeniero mallorquín Eusebio Estada y el general Valeriano Weyler.​ Desde los ocho años escribió narraciones que eran variantes de las historias que le contaba su abuela Caterina en su infancia.2​ Se trasladó en 1965 a estudiar filología hispánica a la Universidad Autónoma de Barcelona (UAB), donde se licenció en Filosofía y Letras y se doctoró en Filología Hispánica con Premio Extraordinario por la UAB. Riera participó en las movilizaciones estudiantiles en contra del franquismo, de la guerra de Vietnam y en el incipiente movimiento feminista.​ Todo ello forjó su mirada sobre la realidad, una mirada que cuestionaba las normas, bajo el influjo de mayo del 68 francés, y que vislumbraba una ventana abierta a un mundo radicalmente diferente.")]),_c('p',[_vm._v("En 1995 se convirtió en catedrática de literatura española de su alma máter y desde 2002 ejerce como directora de la Cátedra José Agustín Goytisolo en esta misma universidad. Riera ha estudiado a lo largo de su carrera la literatura española del Siglo de Oro y de la literatura catalana de la Escuela de Barcelona. Ha sido catedrática de Lengua y Literatura Españolas de Institutos Nacionales de Enseñanza Media y titular de universidad; profesora visitante en las universidades de Florida (Gainesville) (1987), Darmouth College (2001) y en la University of Chicago (2006). Ha impartido cursos y seminarios, entre otras, en la Universidad Internacional Menéndez Pelayo; Complutense de Madrid; Aarhus; Instituto de Estudios Hispánicos de Amberes; Universidad de Puerto Rico (Mayagüez); la Sorbona; Uppsala; Harvard; Montclair; Cornell; University of Misuri-St. Louis; Columbia; Indiana y The Catholic University of America (Washington), entre otras.")]),_c('p',[_vm._v("Publicó su primer libro, la recopilación de cuentos Te deix, amor, la mar com a penyora, en 1975 (el relato que le da título había ganado, en 1974, el premio Recull-Francesc Puig i Llensa de narració), Riera aportó un estilo nuevo y fresco y utilizaba el habla mallorquina coloquial para sugerir y crear una narrativa que ponía sobre la mesa temas que hasta entonces habían sido tabúes, como el amor entre las mujeres, y al mismo tiempo era bastante crítica con la sociedad del momento.2​ Fue seguido, dos años después por otra recopilación titulada Pongo las gaviotas por testigo, un conjunto de narraciones que seguían los mismos principios narrativos de la obra anterior que cierra su primera etapa de la producción literaria de la autora.")]),_c('p',[_vm._v(" Su primera novela, Una primavera per a Domenico Guarini, con la que recibió el Premio Prudenci Bertrana en 1980, abre la segunda etapa de la obra de Riera, que comprende la producción literaria de la década de los ochenta. Esta primera novela no sólo representa un cambio de género sino también de objetivo, el de formular un modelo de novela culta alternada con elementos coloquiales y el de experimentar con la simbiosis de registros y de géneros -la narrativa policíaca y el ensayo, el lenguaje culto y el periodístico-. Esta voluntad experimentadora e investigadora de la autora, y una actitud de juego, con una mirada a menudo lúdica e irónica, son los ejes de las obras de este periodo, como la recopilación de narraciones Epitelis tendríssims y las novelas Qüestió d'amor propi y Joc de miralls.")]),_c('p',[_vm._v("Con las novelas históricas Dins el darrer blau y Cap al cel obert, con buena acogida por la crítica, se inicia la tercera etapa. Ambas novelas construyen la doble identidad de judíos y mallorquines de los protagonistas, a partir de dos historias enlazadas: la primera, ambientada en la Mallorca de finales del siglo XVII, narra la persecución de un grupo de judíos condenados a la quema pública en la hoguera por la Inquisición; la segunda tiene como protagonistas a los descendientes de los judíos del siglo XVII establecidos en la isla de Cuba en pleno conflicto colonial.")]),_c('p',[_vm._v("Con estas dos ambiciosas narraciones, Riera reconstruyó con todo detalle y rigor los escenarios históricos de aquel momento. La escritora desplegó historias de ficción y trazó con mucho talento el carácter y perfil de los diferentes personajes. Ambas obras tienen un gran valor literario y testimonian una excelente trayectoria literaria, que se consolida definitivamente en la segunda mitad de la década de los noventa.​ Riera escribe sus novelas y relatos en catalán, y se encarga de traducirlas al castellano, mientras que los ensayos los escribe en castellano.4​ Gran parte de su producción se ha traducido a una docena de idiomas, como alemán, árabe, francés, inglés e italiano")]),_c('h2',[_vm._v("Reconocimientos")]),_c('p',[_vm._v("El 19 de abril de 2012 fue elegida miembro de número de la Real Academia Española, donde pasó a ocupar la silla n tras leer su discurso de ingreso titulado Sobre un lugar parecido a la felicidad el 7 de noviembre de 2013.7​8​9​ También es miembro de número de la Real Academia de Buenas Letras de Barcelona.")]),_c('p',[_vm._v("Riera ha recibido numerosos premios por sus obras, entre otros, el Ramon Llull (1989) por Joc de miralls; el Premio Nacional de Narrativa (1995) por Dins el darrer blau o el Premio Sant Jordi (2003) por La meitat de l'ànima. Ha colaborado con diversas publicaciones, como el diario El País o las revistas Quimera y Serra d'Or, entre otras. En noviembre de 2015 recibió el prestigioso Premio Nacional de las Letras Españolas10​ El 28 de febrero de 2018 el Gobierno de la Comunidad Autónomas de las Islas Baleares le otorgó la Medalla de Oro de la comunidad por su trayectoria como miembro de la Real Academia Española e integrante del movimiento de la literatura catalana escrita por mujeres en una ceremonia en el Palacio de Congresos de Palma.")]),_c('h2',[_vm._v("Obras")]),_c('b-row',[_c('b-col',{attrs:{"sm":"12"}},[_c('h3',[_vm._v("Novelas")]),_c('ul',{staticClass:"novelas"},_vm._l((_vm.novelas),function(novela){return _c('li',[_vm._v(_vm._s(novela)+" ")])}),0)]),_c('b-col',{attrs:{"sm":"12"}},[_c('h3',[_vm._v("Crítica literaria y ensayo")]),_c('ul',{staticClass:"criticas"},_vm._l((_vm.criticas),function(critica){return _c('li',[_vm._v(_vm._s(critica))])}),0)])],1)],1)}
